			// 		var s = pointInsideCircle([that.latitude,that.longitude],[that.circles[0].latitude,that.circles[0].longitude],that.r/10000);
			// 		that.is = s;
			// 	}
			// });
		},
		handler ({BMap, map}) {
			var that=this
		      // var point = new BMap.Point(109.49926175379778, 36.60449676862417)
