		      // var circle = new BMap.Circle(point, 6, { strokeColor: 'Red', strokeWeight: 6, strokeOpacity: 1, Color: 'Red', fillColor: '#f03' })
		      // map.addOverlay(circle)
			  var geolocation = new BMap.Geolocation();
